<template>
  <div class="section">
      <el-date-picker
        v-model="date"
        type="year"
        format="yyyy 年"
        value-format="yyyy"
        @change="getData"
        placeholder="选择年份">
        </el-date-picker>
      <div class="tabs">
          <div :class="{active:tab == 1}" @click="tab = 1">按照队伍统计</div>
          <div :class="{active:tab == 2}" @click="tab = 2">按照项目部统计</div>
      </div>
      <el-table :data="team" border style="width: 100%" v-show="tab == 1">
          <el-table-column prop="team" label="队号" />
          <el-table-column prop="project" label="项目部" />
          <el-table-column prop="start_drill_count" label="开钻井数" />
          <el-table-column prop="start_drill_no" label="开钻井号" />
          <el-table-column prop="completion_count" label="完井井数" />
          <el-table-column prop="completion_no" label="完井井号" />
          <el-table-column prop="acc_footage" label="累计进尺（m）" />
          <el-table-column prop="avg_completion_depth" label="平均完钻井深（m）" />
          <el-table-column prop="max_completion_depth" label="最大完钻井深（m）" />
          <el-table-column prop="drilling_period" label="平均钻井周期（d）" />
          <el-table-column prop="completion_period" label="平均完井周期（d）" />
          <el-table-column prop="rop" label="平均机械钻速（m/h）" />
          <el-table-column prop="accident_ratio" label="事故复杂率" />
      </el-table>
    
      <el-table :data="project" border style="width: 100%" v-show="tab == 2">
          <el-table-column prop="project" label="项目部" />
          <el-table-column prop="rig_count" label="动用钻机数" />
          <el-table-column prop="start_drill_count" label="开钻井数" />
          <el-table-column prop="completion_count" label="完井井数" />
          <el-table-column prop="avg_completion_depth" label="平均完钻井深（m）" />
          <el-table-column prop="acc_footage" label="累计进尺（m）" />
           <el-table-column prop="drilling_period" label="平均钻井周期（d）" />
          <el-table-column prop="completion_period" label="平均完井周期（d）" />
          <el-table-column prop="rop" label="平均机械钻速（m/h）" />
      </el-table>
     
  </div>
</template>

<script>
import request from "@/utils/request";
export default {
  props: ["route"],
  data () {
     return {
        tab:1,
        team:[],
        project:[],
        date: '2018'
      }
  },

  components: {},

  computed: {},

  mounted: function() {
    this.getData()
  },

  methods: {
    getData() {
      let data = this.route;
      request({
        url: "/report/year",
        method: "get",
        params: {
          ...data,
          year: this.date
        }
      }).then(res => {
          for(let i in res){
            this[i] = res[i]
          }
      });
    }
  }
}
</script>



